import * as vscode from 'vscode';
import * as osc from 'osc';
import { config as cfg } from '../config/index';

export type OscArgs = Array<{ type: string; value?: any } | number | string | boolean>;
export type BroadcastFn = (address: string, args: any[]) => void;
export type OscConfig = {
    localAddress: string;
    remoteAddress: string;
    localPort: number;
    remotePort: number;
};

export interface OscServiceApi {
    send(address: string, args: any[]): void;
    reload(): void;
    dispose(): void;
    getConfig(): OscConfig;
    handleConfigChange(e: vscode.ConfigurationChangeEvent): void;
}
let port: osc.UDPPort | null = null; // receiver
let sendPort: osc.UDPPort | null = null; // sender
let broadcast: BroadcastFn | null = null;
let output: vscode.OutputChannel | null = null;
let runningConfig: OscConfig | null = null;

function getConfig(): OscConfig {
    // Return the actual running config (including overrides)
    return runningConfig || cfg.getOscConfig();
}

import { showAndTrackOutputChannel } from '../logging/output';
function ensureOutput(focus: boolean = false) {
    if (!output) output = vscode.window.createOutputChannel('P5 STUDIO: OSC');
    if (focus) {
        showAndTrackOutputChannel(output!);
    }
    return output!;
}

function openPort(
    override?: { localAddress?: string; localPort?: number; remoteAddress?: string; remotePort?: number },
    focusOutput: boolean = false
) {
    // Close previous ports if any
    if (port) { try { port.close(); } catch { } port = null; }
    if (sendPort) { try { sendPort.close(); } catch { } sendPort = null; }
    let baseCfg = cfg.getOscConfig();
    let localAddress = (override && override.localAddress) ? override.localAddress : baseCfg.localAddress;
    let localPort = (override && typeof override.localPort === 'number') ? override.localPort : baseCfg.localPort;
    let remoteAddress = (override && override.remoteAddress) ? override.remoteAddress : baseCfg.remoteAddress;
    let remotePort = (override && typeof override.remotePort === 'number') ? override.remotePort : baseCfg.remotePort;
    runningConfig = { localAddress, localPort, remoteAddress, remotePort };

    const receiveOptions = {
        remoteAddress: "127.0.0.1", // required by osc.js
        remotePort: Math.floor(20000 + Math.random() * 40000), // random high port
        localAddress,
        localPort,
        metadata: true
    };
    port = new osc.UDPPort(receiveOptions);

    const sendOptions = {
        remoteAddress,
        remotePort,
        localAddress: "127.0.0.1", // required by osc.js
        localPort: Math.floor(20000 + Math.random() * 40000), // random high port
        metadata: true
    };
    sendPort = new osc.UDPPort(sendOptions);
    const ch = ensureOutput(focusOutput);
    // ch.appendLine(`[DEBUG] Opening OSC receive port on ${localAddress}:${localPort}`);
    // ch.appendLine(`[DEBUG] Opening OSC send port to ${remoteAddress}:${remotePort}`);
    port.open();
    sendPort.open();

    port.on('ready', () => {
        try {
            ch.appendLine(`[${new Date().toLocaleTimeString()}] [OSC] Listening on ${localAddress}:${localPort}`);
        } catch { }
    });
    sendPort.on('ready', () => {
        try {
            ch.appendLine(`[${new Date().toLocaleTimeString()}] [OSC] Sending to ${remoteAddress}:${remotePort}`);
        } catch { }
    });
    port.on('message', (msg: any) => {
        try { ch.appendLine(`[${new Date().toLocaleTimeString()}] [OSC] Received @ ${msg.address} ${JSON.stringify(msg.args)}`); } catch { }
        if (broadcast) {
            try { broadcast(msg.address, msg.args); } catch (e) { try { ch.appendLine(`[DEBUG] Broadcast error: ${e}`); } catch { } }
        }
    });
    try {
        (port as any).on && (port as any).on('error', (err: any) => {
            try {
                const msg = err && err.message ? err.message : String(err);
                ch.appendLine(`[${new Date().toLocaleTimeString()}] [OSC] Error: ${msg}`);
                if (typeof err?.code === 'string' && err.code.toUpperCase() === 'EADDRINUSE') {
                    ch.appendLine('[OSC] Hint: The local port is already in use. Close the other app or change P5Studio.oscLocalPort or P5Studio.oscLocalAddress (e.g., 127.0.0.1).');
                }
            } catch { }
        });
        (sendPort as any).on && (sendPort as any).on('error', (err: any) => {
            try {
                const msg = err && err.message ? err.message : String(err);
                ch.appendLine(`[${new Date().toLocaleTimeString()}] [OSC] SendPort Error: ${msg}`);
            } catch { }
        });
    } catch { }
}

function toPacket(address: string, args: any[]) {
    return {
        address,
        args: (args || []).map((a: any) => {
            if (typeof a === 'number') return { type: 'f', value: a };
            if (typeof a === 'string') return { type: 's', value: a };
            if (typeof a === 'boolean') return { type: a ? 'T' : 'F', value: a };
            return { type: 's', value: String(a) };
        })
    };
}

// Accepts optional override: { localAddress, localPort, remoteAddress, remotePort }
export function initOsc(bcast: BroadcastFn, override?: { localAddress?: string; localPort?: number; remoteAddress?: string; remotePort?: number }): OscServiceApi {
    broadcast = bcast;
    openPort(override, true);

    function send(address: string, args: any[]) {
        const ch = ensureOutput();
        try {
            if (!sendPort) {
                vscode.window.showErrorMessage('OSC send port is not initialized. Try reloading the window.');
                return;
            }
            const packet = toPacket(address, args);
            ch.appendLine(`[OSC] Sending to address ${address}: ${JSON.stringify(packet)}`);
            sendPort.send(packet);
        } catch (e) {
            try { ch.appendLine(`[OSC] OSC send error: ${e}`); } catch { }
            console.error('OSC send error:', e);
        }
    }

    function reload() { openPort(undefined, false); }

    function dispose() {
        const ch = ensureOutput(true);
        try { if (port) port.close(); } catch { }
        try { if (sendPort) sendPort.close(); } catch { }
        port = null;
        sendPort = null;
        runningConfig = null;
        try {
            const cfg = getConfig();
            ch.appendLine(`[${new Date().toLocaleTimeString()}] [OSC] Server stopped on ${cfg.localAddress}`);
        } catch { }
    }

    function handleConfigChange(e: vscode.ConfigurationChangeEvent) {
        if (
            e.affectsConfiguration('P5Studio.oscLocalAddress') ||
            e.affectsConfiguration('P5Studio.oscRemoteAddress') ||
            e.affectsConfiguration('P5Studio.oscRemotePort') ||
            e.affectsConfiguration('P5Studio.oscLocalPort')
        ) {
            reload();
        }
    }

    return { send, reload, dispose, getConfig, handleConfigChange };
}
